import b2bFooter from "../../components/b2bfooter.vue";
export default {
	components:{
		b2bFooter
	},
	data: function() {
		return {
		 
			pageLoad: false,
			pageData: {},
			catid:0,
			height:440,
			sideTop:44
		}
	},
	onLoad: function(ops) {
		var win=uni.getSystemInfoSync();
		this.height=win.windowHeight-50;

		this.sideTop=0;

		this.getPage();
		
	},
	methods: {
		setCat:function(catid){
			this.catid=catid;
		},
		goList:function(catid){
			uni.navigateTo({
				url:"../b2b_product/list?catid="+catid
			})
		},
		getPage: function() {
			var that = this;
			that.app.get({
				url: that.app.apiHost + "/module.php?m=b2b_category&ajax=1",
				success: function(res) {
					that.pageData = res.data;
					for(var i in res.data.catList){
						that.catid=res.data.catList[i].catid;
						break;
					}
					
					that.pageLoad = true;
				}
			})
		}
	}
}
